import React, { useState, useEffect } from "react";

const App = () => {
  const [weatherData, setWeatherData] = useState(null);
  const API_KEY = "0e00a41b43fdc7c6676f524ddf050d25";
  const lat = 63.41244;
  const lon = 10.40062;

  useEffect(() => {
    const fetchWeatherData = async () => {
      const response = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${API_KEY}&units=metric&lang=no`
      );
      const data = await response.json();

      setWeatherData(data);
    };

    fetchWeatherData();
  }, []);

  //Hvordan få gjort om temp til Celsius?
  //const tempCelsius = weatherData.main.temp - 32 * (5 / 9);
  //Hehe, var visst bare å spørre etter units=metric :p

  return (
    <div>
      {weatherData ? (
        <div>
          <p>Temperature: {weatherData.main.temp}</p>
          <p>Humidity: {weatherData.main.humidity}</p>
          <p>Temp Min: {weatherData.main.temp_min}</p>
          <p>Temp Max: {weatherData.main.temp_max}</p>
          <p>Wind Speed: {weatherData.wind.speed}</p>
          <p>Base: {weatherData.base}</p>
          <p>Name: {weatherData.name}</p>
          <p>Pressure: {weatherData.main.pressure}</p>
        </div>
      ) : (
        "Loading weather data..."
      )}
    </div>
  );
};

export default App;
